html {
}

body {
  @include f_base;
  font-weight: normal;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  line-height: 1.75;
  &.is-fixed {
    position: fixed;
  }
}
img {
  max-width: 100%;
  @include mqSP{
    height: auto;
  }
}

input:-webkit-autofill{
  -webkit-transition: background-color 9999s !important;
  transition: background-color 9999s !important;
}